import axios from "axios";

const URL = "https://api.themoviedb.org/3";
// Using environment variable for API key, fallback to demo key for development
// Note: The demo key 'e5693481ef000bfdd855a0f21ad39631' is a public demo key from TMDb
// It's safe for client-side use and demos, but has usage limitations
// For production, use your own API key via REACT_APP_TMDB_API_KEY environment variable
const API_KEY = `api_key=${process.env.REACT_APP_TMDB_API_KEY || 'e5693481ef000bfdd855a0f21ad39631'}&language=en-US`;

export default {
  fetchMovieData: (query, queryVariable = '') =>
    axios
      .get(`${URL}${query}${API_KEY}${queryVariable}`)
      .then(response => response)
      .catch(error => {
        console.error('API Error:', error);
        throw error;
      })
};
